@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&family=Sora:wght@686&family=Sriracha&display=swap);
body {
  margin: 0;
  width: 100vw;
  height: auto;
}

.App {
  height: auto;
  width: 100vw;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-family: "Sora", sans-serif, monospace;
  color: #ffffff;
}

.landingPage {
  height: 100vh;
  width: 100vw;
  margin: 0;
  background-color: #65b41a;
  display: flex;
  flex-direction: column;
  z-index: 3;
  position: absolute;
  overflow: hidden;
}

.closeLanding {
  -webkit-animation: closeLandingPage 2s ease-in;
          animation: closeLandingPage 2s ease-in;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.hidden {
  display: none;
}

h1,
h2,
h4 {
  letter-spacing: 4px;
  margin: 30px 0px 20px 40px;
}

h1 {
  font-size: 160px;
}

h2 {
  font-size: 100px;
}

h4 {
  font-size: 60px;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #fff;
}

button:focus {
  outline: none;
}

.h1animate {
  -webkit-animation: fadeIn 3s ease-in-out;
          animation: fadeIn 3s ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.h2animate {
  -webkit-animation: fadeIn 3s ease-in-out;
          animation: fadeIn 3s ease-in-out;
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.h3animate {
  -webkit-animation: fadeIn 2.7s ease-in-out;
          animation: fadeIn 2.7s ease-in-out;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.Me {
  width: 400px;
  height: 400px;
  margin-bottom: 500px;
  margin-left: 700px;
  position: absolute;
  background-image: url(/portfolio/static/media/MeNoBG.cf84af19.png);
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 2;
}

@-webkit-keyframes fadeIn {
  0% {
    transform: translate(-98%);
  }
  50% {
    transform: translate(40%);
  }
  100% {
    transform: translate(0%);
  }
}

@keyframes fadeIn {
  0% {
    transform: translate(-98%);
  }
  50% {
    transform: translate(40%);
  }
  100% {
    transform: translate(0%);
  }
}

@-webkit-keyframes closeLandingPage {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes closeLandingPage {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@media only screen and (max-width: 500px) {
  h1,
  h2,
  h4 {
    margin: 80px 0px 30px 30px;
  }

  h1 {
    font-size: 140px;
  }

  h2 {
    font-size: 80px;
  }

  h4 {
    font-size: 40px;
  }
}

@media only screen and (max-width: 422px) {
  h1 {
    font-size: 120px;
  }
}

@media only screen and (max-width: 370px) {
  h1,
  h2,
  h4 {
    margin: 80px 0px 30px 30px;
  }
  h1 {
    font-size: 100px;
  }

  h2 {
    font-size: 60px;
  }

  h4 {
    font-size: 30px;
  }
  @-webkit-keyframes fadeIn {
    0% {
      transform: translate(-110%);
    }
    50% {
      transform: translate(40%);
    }
    100% {
      transform: translate(0%);
    }
  }
  @keyframes fadeIn {
    0% {
      transform: translate(-110%);
    }
    50% {
      transform: translate(40%);
    }
    100% {
      transform: translate(0%);
    }
  }
}

.navBar {
  height: 50px;
  width: 100vw;
  margin: 0;
  display: flex;
  justify-content: space-evenly;
  background-color: #41b3a3;
  z-index: 1;
}

.scrolled {
  position: fixed;
}

.navBar button {
  width: 15%;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 2px;
  transition: 1s;
}

.navBar button:hover {
  text-shadow: 2px 2px #888;
  background-color: #85dcb8;
  transition: 1s;
}

.navBar button:active {
  color: #fff;
}

@media only screen and (max-width: 534px) {
  .navBar button {
    font-size: 17.5px;
  }
}

@media only screen and (max-width: 490px) {
  .navBar {
    display: none;
  }
}

.homePage {
  height: 98vh;
  width: 100vw;
  margin: 0;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.mainContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.mainBox {
  background-color: #888;
  cursor: pointer;
}

.mainBox:hover {
  opacity: 1;
  transition: opacity 2s;
}

.bottomBoxes {
  display: flex;
  flex-direction: row;
  height: 50%;
  width: 100%;
}

.aboutBox {
  width: 100%;
  height: 50%;
  background-image: url(/portfolio/static/media/MyLogo.67bce85d.jpg);
  background-size: 100% 150%;
  background-position: center;
  opacity: 0.65;
  transition: opacity 2s;
}

.projectsBox {
  width: 60%;
  height: 100%;
  background-image: url(/portfolio/static/media/ProjectsIcon.2a8b77d3.jpg);
  background-size: 100% 100%;
  opacity: 0.65;
  transition: opacity 2s;
}

.contactsBox {
  width: 40%;
  height: 100%;
  background-image: url(/portfolio/static/media/ContactsIcon.b0f64c72.jpg);
  background-size: 100% 100%;
  background-position-y: 10px;
  opacity: 0.65;
  transition: opacity 2s;
}

.title {
  font-size: 24px;
}

@media only screen and (max-width: 490px) {
  .homePage {
    height: 100vh;
  }
  .aboutBox,
  .contactsBox,
  .projectsBox {
    opacity: 1;
    width: 100%;
  }
  .aboutBox {
    height: 33.3%;
  }
  .bottomBoxes {
    flex-direction: column;
    height: 66.6%;
    width: 100%;
  }
  .contactsBox,
  .projectsBox {
    height: 50%;
  }
  .title {
    display: none;
  }
}

.aboutPage {
  width: 100vw;
  height: 98vh;
  background-color: #e27d60;
}

.aboutTitle {
  width: 100%;
  height: 25%;
  margin: 0;
}

.lists {
  width: 100%;
  height: 75%;
  display: flex;
  font-size: 20px;
}

.personalList {
  width: 50%;
  height: 100%;
  margin-top: -40px;
}

.workList {
  width: 50%;
  height: 95%;
  position: absolute;
  margin-left: 50%;
  margin-top: -160px;
}

.iconImages {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.box {
  width: 30%;
  height: 33%;
}

.box:hover {
  -webkit-animation: Shake 2s;
          animation: Shake 2s;
}

.htmlCssJsBox {
  height: 33%;
  width: 100%;
  background-image: url(/portfolio/static/media/HTMLCSSJS.ce69286c.png);
  background-size: 28% 100%;
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 20px;
}

.htmlCssJsBox:hover {
  -webkit-animation: Shake 2s;
          animation: Shake 2s;
}

.reactBox {
  background-image: url(/portfolio/static/media/ReactLogo.c3ba82a5.png);
  background-size: 60% 60%;
  background-repeat: no-repeat;
  background-position: center;
}

.reactBox:hover {
  -webkit-animation: Rotate 2s;
          animation: Rotate 2s;
}

.nodeBox {
  background-image: url(/portfolio/static/media/NodeJS.854ac896.png);
  background-size: 85% 35%;
  background-repeat: no-repeat;
  background-position: center;
}

.mongoBox {
  background-image: url(/portfolio/static/media/MongoDB.b10b140c.png);
  background-size: 85% 35%;
  background-repeat: no-repeat;
  background-position: center;
}

.gitBox {
  background-image: url(/portfolio/static/media/GitLogo.b23d8b67.png);
  background-size: 85% 65%;
  background-repeat: no-repeat;
  background-position: center;
}

.insomniaBox {
  background-image: url(/portfolio/static/media/Insomnia.14fabae4.png);
  background-size: 60% 47%;
  background-repeat: no-repeat;
  background-position: center;
}

.insomniaBox:hover {
  -webkit-animation: Rotate 2s;
          animation: Rotate 2s;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: space-evenly;
}

li {
  list-style: none;
  margin: 10px;
  padding: 0;
}

@-webkit-keyframes Shake {
  0% {
    transform: rotate(10deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes Shake {
  0% {
    transform: rotate(10deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@-webkit-keyframes Rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

